import React, { useState } from "react";
import { FilterText } from "@cvccorp-components/core";
import clsx from "clsx";

const _FilterText = prop => {
    const {
        filters,
        filter,
        index,
        onApply,
        noModal,
        onChange,
        onActive,
        className,
        height,
    } = prop;
    const [item, setItem] = useState(filter);

    const _onOpen = open => {
        const _item = { ...item };
        _item.open = open;
        setItem(_item);
    };

    const _onChange = value => {
        const _item = { ...item };
        _item.value = value;
        setItem(_item);
    };

    const _onClose = () => {
        const _item = { ...item };
        if (!_item.applied) {
            Object.assign(_item, { open: false, value: "" });
        } else {
            Object.assign(_item, { open: false });
        }
        setItem(_item);
    };

    const _onApply = () => {
        const _item = { ...item };
        Object.assign(_item, { open: false, applied: true });
        setItem(_item);
        filters[index] = _item;
        onApply(filters);
    };

    const _onReset = () => {
        const _item = { ...filter };
        Object.assign(_item, { open: item.open, applied: false });
        _item.value = "";
        setItem(_item);
        filters[index] = _item;
        onApply(filters);
    };

    return (
        <FilterText
            onOpen={() => _onOpen(true)}
            onClose={() => _onClose()}
            open={item.open}
            value={item.value ? item.value : ""}
            onChange={value => _onChange(value)}
            onReset={() => _onReset()}
            onApply={() => _onApply()}
            label={item.label}
            className={clsx(className, `FilterText-item-${index}`)}
            height={height}
            onActive={onActive}
        />
    );
};

export default _FilterText;
